*,
*::before,
*::after {
  box-sizing: border-box;
}

*:focus {
  outline: 2px solid #5d16d5;
  outline-offset: 2px;
}

body {
  position: relative;
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  position: relative;
  display: block;
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 16px 6.27%;
  font-size: 18px;
  line-height: 22px;
}

/* interactive elements */

.link:link,
.link:visited {
  color: #000;
  text-decoration: underline;
}

.link:hover,
.link:active {
  color: #5d16d5;
}

@media (prefers-reduced-motion: no-preference) {
  .link {
    transition: color 0.3s ease;
  }
}

.button:not(:disabled):hover,
.button:not(:disabled):active {
  text-decoration: underline;
}

.button {
  position: relative;
  display: block;
  padding: 13px 18px;
  font-size: 22px;
  line-height: 26px;
  text-decoration: none;
  color: #000000;
  background: #c7d2e9;
  border-radius: 4px;
  border: none;
  overflow: hidden;
  z-index: 1;
}

.button:not([disabled]):after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 20, 20, 0.1);
  pointer-events: none;
  z-index: -1;
}

.button:not([disabled]) span {
  position: relative;
  z-index: 2;
}

.button:not([disabled]) {
  cursor: pointer;
}

.button:not([disabled]):hover:after {
  opacity: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .button:not([disabled]):after {
    transition: color 0.3s ease;
  }
}

.button--link {
  text-decoration: underline;
  background-color: transparent;
}

.button--accent {
  color: #fff;
  background: #5d16d5;
  border-radius: 8px;
}

.button--accent a:link,
.button--accent a:visited,
.button--accent a:hover,
.button--accent a:active {
  color: inherit;
  text-decoration: none;
}

/* input styles */
.input__wrapper {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  height: 48px;
  width: 100%;
  background: #f9f9fb;
  border: none;
  box-shadow: inset 0 0 0 1px #c7d2e9;
  border-radius: 4px;
}

.input__container {
  flex-grow: 1;
  flex-shrink: 1;
  height: 100%;
}

.input {
  width: 100%;
  height: 100%;
  padding: 13px 17px;
  margin: 0;
  color: #000;
  background-color: transparent;
  border: none;
  border-radius: inherit;
  z-index: 1;
}

/* typography classes */

.typography--18px {
  font-size: 18px;
  line-height: 22px;
}

.typography--22px {
  font-size: 22px;
  line-height: 26px;
}

.typography--24px {
  font-size: 24px;
  line-height: 28px;
}

.typography--26px {
  font-size: 26px;
  line-height: 30px;
}

/* helper classes */

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.skip-link {
  position: absolute;
  top: -100%;
  left: -100%;
  padding-left: 40px;
  padding-right: 40px;
  z-index: 2;
}

.skip-link:focus {
  top: 4px;
  left: 4px;
}

.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  align-content: flex-end;
  align-items: center;
  padding-top: 8px;
}

.header > * {
  margin-top: 8px;
}

.header__logo {
  display: block;
  min-height: 48px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-right: auto;
}

.search__form {
  max-width: 295px;
}

.search {
  padding-right: 53px;
}

.search__action {
  position: absolute;
  top: 0;
  right: 0;
  flex-grow: 0;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  margin: 0;
  padding: 0;
  color: #000;
  background-color: transparent;
  z-index: 2;
}

.search__action span {
  display: flex;
  flex-flow: row nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.search__icon {
  display: block;
  pointer-events: none;
}

.lang__container {
  display: flex;
  flex-flow: row nowrap;
  padding: 0 18px;
}

.lang:not(:first-of-type) {
  margin-left: 8px;
}

.profile__action {
  width: 48px;
  height: 48px;
  padding: 0;
  background: transparent;
  border-radius: 4px;
  overflow: hidden;
}

.profile__action span {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-content: center;
  align-items: center;
  justify-content: center;
}

@media (prefers-reduced-motion: no-preference) {
  .profile__action {
    transition: background-color 0.3s ease;
  }
}

.footer__container {
  display: block;
  background-color: #f1f1f1;
}

.footer {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  align-content: center;
  justify-content: center;
  padding-top: 73px;
  padding-bottom: 95px;
}

.footer__section {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(50% - 16px);
}

.footer__section:not(:last-of-type) {
  margin-right: 16px;
}

.footer__title {
  margin-bottom: 16px;
}

/* навигация */
.footer-nav {
  margin-bottom: 40px;
}

.footer-nav__item {
  padding: 0;
}

.footer-nav__item:not(:last-of-type) {
  margin-bottom: 16px;
}

/* соцсети */
.footer-social__list {
  margin-left: -4px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  align-content: flex-start;
  justify-content: flex-start;
}

.footer-social__item:not(:last-of-type) {
  margin-right: 8px;
}

.footer-social__link {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  width: 48px;
  height: 48px;
}

.footer-social__link svg {
  color: inherit;
}

/* подписка */
.footer-subscription__row {
  display: block;
  margin-bottom: 8px;
}

.footer-subscription__row:last-of-type {
  margin-bottom: 16px;
}

.footer-subscription__row--checkbox {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: flex-start;
}

.footer-subscription__checkbox {
  flex-grow: 0;
  flex-shrink: 0;
  display: block;
  width: 24px;
  height: 24px;
  background: #f9f9fb;
  border: none;
  box-shadow: inset 0 0 0 1px #c7d2e9;
  border-radius: 4px;
  overflow: hidden;
}

.footer-subscription__checkbox + span {
  margin-left: 8px;
  line-height: 24px;
}

.nav-main {
  display: block;
  width: 100%;
}

.nav-main__list {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: flex-start;
  align-content: center;
}

.nav-main__item:not(:last-of-type) {
  margin-right: 27px;
}

.nav-main__link {
  min-height: 48px;
  margin-left: -13px;
  padding: 13px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  align-content: center;
  justify-content: center;
}


.exhibitions__filters {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: flex-start;
  padding-bottom: 8px;
}

.exhibitions__cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(312px, 100%), 312px));
  align-items: start;
  align-content: start;
  justify-content: start;
  grid-gap: 40px;
  gap: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.exhibition-card {
  display: block;
  flex-grow: 0;
  flex-shrink: 0;
  width: 100%;
  max-width: 312px;
}

.exhibition-card__img {
  width: 100%;
  max-width: 312px;
  height: auto;
  max-height: 148px;
}

.exhibition-card__title {
  margin-top: 24px;
  margin-bottom: 16px;
}

.exhibition-card__date {
  margin-bottom: 8px;
}

.exhibition-filters__button:not(:first-of-type) {
  margin-left: 8px;
}

.stream {
  position: relative;
  display: block;
  height: 0;
  margin: 0 auto;
  padding-bottom: 56.25%;
  overflow: hidden;
}

.stream iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  border-width: 0;
  outline-width: 0;
}


.article {
  margin-bottom: 120px;
}

.container.article {
  padding-bottom: 0;
}

.article__title {
  margin-bottom: 40px;
  font-size: 50px;
  line-height: 59px;
}

.article__title a.link {
  text-decoration: none;
}


